<template>
  <div>
    <van-grid
      :border="true"
       class="image-m"
      :column-num="3"
      :gutter="10"
      square>
      <van-grid-item
        v-for="(item, index) in ImageList"
        class="imgbox"
       :key="index">
        <van-image
          class="imagesize"
          @click="OnImageCliak(index)"
          :src='item.url' />
        <van-icon
          name="delete-o"
          class="imgitem"
          @click="OnDeleteImage(item.id)" />
      </van-grid-item>
    </van-grid>
    <el-upload
      class="upload-demo"
      action="http://api-toutiao-web.itheima.net/mp/v1_0/user/images"
      :headers="uploadHeaders"
      name="image"
      :limit="1"
      :show-file-list="false"
      :on-success="OnImgSuccess">
      <el-button size="small" class="imgupdown" type="primary">点击上传</el-button>
    </el-upload>
  </div>
</template>

<script>
import { ImagePreview, Toast } from 'vant'
import { getImages, deleteImage } from '@/api/image'
export default {
  name: 'ImageIndexM',
  components: {},
  props: {},
  data () {
    const user = JSON.parse(window.localStorage.getItem('user'))
    return {
      ImageList: [],
      uploadHeaders: {
        Authorization: `Bearer ${user.token}`
      }
    }
  },
  computed: {},
  watch: {},
  created () {
    this.loadImages(1)
  },
  mounted () {},
  methods: {
    OnImageCliak (index) {
      ImagePreview([this.ImageList[index].url])
    },
    async loadImages (page) {
      const res = await getImages({
        collect: false,
        page,
        per_page: 50
      })
      const data = res.data.data.results
      this.ImageList = data
    },
    OnDeleteImage (img) {
      try {
        deleteImage(img).then((res) => {
          this.loadImages(this.page)
          Toast.success('删除成功')
        })
      } catch (error) {
        Toast.fail('删除失败')
      }
    },
    OnImgSuccess () {
      Toast.success('上传成功')
      this.loadImages(1)
    }
  }
}
</script>

<style scoped lang="less">
.image-m{
  position: fixed;
  left: 0;
  right: 0;
  bottom: 90px;
  top: 50px;
  overflow-y: auto;
  .imgbox{
    position: relative;
    .imagesize{
      width: 100px;
      height: 100px;
    }
    .imgitem{
      position: absolute;
      bottom: 1%;
    }
  }
}
.upload-demo{
  position: fixed;
  bottom: 7%;
  width: 100%;
  background-color: #fff;
  /deep/.el-upload--text{
    width: 100%;
    button{
      width: 100%;
    }
  }
}
</style>
